* {
    box-sizing: border-box;
      margin: 0;
}

.card-bg{
    background-color: #1A1B21;
}
.profile-img{
 width: 100%;
 height: 317px;
 object-fit: cover;
  border-top-left-radius:5px; 
border-top-right-radius:5px;
 
}
.profile-img-card{
    width: 317px;
    border-top-left-radius:50px; 
    border-top-right-radius:50px; 

    
}
.profile-name{
    text-align: center
}
h4{
    color: #F3BF99;
    font-weight: 400;
    font-size: 0.75rem;
    
}
.link{
    padding: 10px;
    font-weight: 400;
     font-size: 0.688rem;
     color: #F5F5F5
}
.button{
    display: flex;
    justify-content: space-around;
    padding: 10px;
}
.btn{
    background: #FFFFFF;


border: 1px solid #D1D5DB;
box-sizing: border-box;
width: 115px;
height: 34px;

box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
border-radius: 6px;
}
.btn-primary{
    background-color: #5093E2;
    border: 1px solid #5093E2;
}
i{
    padding: 5px;
}
.fa-brands{
    color: white;
}

h1{
    color: white;
    
}
h2{
font-family: 'Inter';

font-weight: 700;
font-size: 1rem;
line-height: 150%;
    color: #F5F5F5;
}
.info{
 
min-width: 247px;
height: 201px;
display: flex;
flex-direction: column;
justify-content: space-around;
padding: 30px;

}
.info p{
    color:#DCDCDC;
    font-family: 'Inter';

font-weight: 400;
font-size: 12px;
line-height: 150%;
padding-bottom: 10px;
padding-top: 7px;
}
body{
    font-family: 'Inter', sans-serif;
    background-color: #23252C;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
main{
    margin: 1rem;
    background-color: #1A1B21;
    max-width: 317px;
     border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    
}
footer{
    background-color: #161619;
    height: 64px;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
footer a{
    margin: 10px;
}
